<template>
  <div class="lr">
    <div class="lr-box">
      <h2>找回密码</h2>
      <van-field
        label="手机号"
        clearable
        maxlength="11"
        v-model="mobile"
        placeholder="请输入手机号"
      ></van-field>
      <van-field
        label="水印码"
        maxlength="6"
        v-model="captchaValue"
        placeholder="请输入水印码"
      >
        <img
          slot="button"
          class="lr-water-mark"
          :src="captureObj.base64Image"
          @click="getNewWaterCode"
        />
      </van-field>
      <van-field
        label="验证码"
        maxlength="6"
        v-model="validCode"
        :placeholder="validCodePlaceholder"
      >
        <van-button slot="button" size="small" type="info" @click="getValidCode"
          >获取验证码</van-button
        >
      </van-field>
      <van-field
        label="密码"
        :right-icon="closeEye ? 'closed-eye' : 'eye-o'"
        :type="closeEye ? 'password' : 'text'"
        @click-right-icon="onClickRightIcon"
        maxlength="6"
        v-model="password"
        :placeholder="'请输入' + passwordLen + '位密码'"
      ></van-field>
      <van-field
        label="确认密码"
        :right-icon="closeEyeC ? 'closed-eye' : 'eye-o'"
        :type="closeEyeC ? 'password' : 'text'"
        @click-right-icon="onClickRightIconC"
        maxlength="6"
        v-model="confirmPassword"
        :placeholder="'请再次输入' + passwordLen + '位新密码'"
      ></van-field>
      <div class="lr-btn" @click="onClickReset">
        确认
      </div>
    </div>
  </div>
</template>

<script>
import { Field, Cell, CellGroup, Image } from "vant";
import gql from "graphql-tag";
import { mapState } from "vuex";
export default {
  name: "Reset",
  components: {
    [Field.name]: Field,
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup,
    [Image.name]: Image
  },
  computed: {
    ...mapState(["orgId", "mallId", "templateId"])
  },

  data() {
    return {
      mobile: "",
      captureObj: {
        base64Image: "",
        captchaId: ""
      },
      captchaValue: "",
      password: "",
      confirmPassword: "",
      closeEye: true,
      closeEyeC: true,
      validCode: "",
      validCodePlaceholder: "请输入短信验证码",
      passwordLen: 6
    };
  },

  mounted() {
    this.getNewWaterCode();
  },
  methods: {
    //输入密码icon
    onClickRightIcon() {
      this.closeEye = !this.closeEye;
    },
    //再次输入密码icon
    onClickRightIconC() {
      this.closeEyeC = !this.closeEyeC;
    },

    //获取新的验证码
    getNewWaterCode() {
      const vm = this;
      this.$apollo
        .query({
          query: gql`
            query resetCaptcha {
              captcha {
                number(len:4,width:78,height:28) {
                  base64Image
                  captchaId
                }
              }
            }
          `,
          fetchPolicy: "network-only"
        })
        .then(({ data }) => {
          vm.captureObj = data.captcha.number;
        });
    },
    //获取短信验证码
    getValidCode() {
      if (!this.mobile) {
        this.$toast("手机号不能为空");
        return;
      }
      if (!this.captchaValue) {
        this.$toast("请填写水印码");
        return;
      }
      const mutation = gql`
        mutation resetGetValidCode(
          $orgId: String!
          $mobile: String!
          $captchaId: String!
          $captchaCode: String!
        ) {
          member{
            sendSms {
              resetPasswordCode(
                orgId: $orgId
                mobile: $mobile
                captchaId: $captchaId
                captchaCode: $captchaCode
              ) 
            }
          }
        }
      `;
      this.$apollo
        .mutate({
          mutation: mutation,
          variables: {
            orgId: this.orgId,
            mobile: this.mobile,
            templateId: this.templateId,
            captchaId: this.captureObj.captchaId,
            captchaCode: this.captchaValue
          }
        })
        .then(
          ({
            data: {
              member: { sendSms:{resetPasswordCode} }
            }
          }) => {
            if (resetPasswordCode == "1") {
              this.validCodePlaceholder = "发送成功";
            }
          }
        );
    },

    //确认重置密码
    async onClickReset() {
      if (!this.mobile) {
        this.$toast("手机号不能为空");
        return;
      }
      if (!this.captchaValue) {
        this.$toast("请输入水印码");
        return;
      }
      if (!this.validCode) {
        this.$toast("请输入短信验证码");
        return;
      }
      const len = this.passwordLen;
      if (this.password.length < len) {
        this.$toast("请输入" + len + "位密码");
        return;
      }
      if (this.confirmPassword.length < len) {
        this.$toast("请输入" + len + "确认密码");
        return;
      }
      if (this.password != this.confirmPassword) {
        this.$toast("两次输入密码不一致");
        return;
      }
      const mutation = gql`
        mutation resetPasswrod(
          $orgId: String!
          $mobile: String!
          $code: String!
          $newPassword: String!
        ) {
          member {
            smsResetPassword(
              orgId: $orgId
              mobile: $mobile
              code: $code
              newPassword: $newPassword
            ) 
          }
        }
      `;
      const result = await this.$apollo
        .mutate({
          mutation: mutation,
          variables: {
            orgId: this.orgId,
            mobile: this.mobile,
            code: this.validCode,
            newPassword: this.password
          }
        })
        let {
            data: {
              member: { smsResetPassword }
            }
          } = result
          if (smsResetPassword =="1") {
            this.$toast.success("密码重置成功");
            let second = 2;
            const timer = setInterval(() => {
              second--;
              if (second == 0) {
                clearInterval(timer);
                this.$router.push({
                  path: "/login"
                });
              }
            }, 1000);
          }
    }
  }
};
</script>

<style lang="less" scoped>
@import url("../assets/css/login.less");
</style>
